/*
@import 'libs/bootstrap.min.css';
*/

*,
*:before,
*:after {
  padding: 0px;
  margin: 0px;
  box-sizing: border-box;
}

.visually-hidden {
  width: 1px;
  height: 1px;
  margin-top: -1px;
  margin-left: -1px;
  overflow: hidden;
  position: absolute;
  clip: rect(0, 0, 0, 0);
}

.alert-primary {
  background-color: red;
}

.my-card {
  margin-bottom: 20px;
  background-color: #000;
}

.content {
  height: 100px;
  border-radius: 10px;
  background-color: crimson;
}

.my-smart-grid {
  margin-top: 30px;
}

.my-wrapper {
  max-width: 1200px;
  margin: 0 auto;
  padding-left: 30px;
  padding-right: 30px;
}

.my-row {
  display: flex;
  flex-wrap: wrap;
  margin-left: -10px;
  margin-right: -10px;
}

.my-row .content {
  margin-bottom: 20px;
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 10px;
  margin-right: 10px;
  width: calc(16.666666666666668% - 20px);
  margin-left: calc(8.333333333333334% + 10px);
}

@media screen and (max-width: 1100px) {
  .my-row {
    justify-content: flex-end;
  }
}

@media screen and (max-width: 960px) {
  .my-row .content {
    width: calc(33.333333333333336% - 20px);
  }
}

@media screen and (max-width: 780px) {
  .my-wrapper {
    padding-left: 15px;
    padding-right: 15px;
  }

  .my-row .content {
    width: calc(50% - 20px);
  }
}

@media screen and (max-width: 560px) {
  .my-row {
    justify-content: center;
  }

  .my-row .content {
    width: calc(83.33333333333334% - 20px);
  }
}